interface IFindIndexOptions {
    /**
     * index position to start search
     */
    startIndex?: number;
}

interface IFindIndexResult {
    start: { begin: number; end: number; };
    end: { begin: number; end: number; };
    between: string;
    fullLength: number;
}
export class StringHelper {
    /**
     * 
     * @param searchString string to be searched
     * @param start string to search as start -> <<module:
     * @param end string to search as end -> >>
     * @param options 
     * @returns 
     */
    public static findIndexes(searchString: string, start: string, end: string, options: IFindIndexOptions) {
        const startBeginIndex = searchString.indexOf(start, options.startIndex);
        if (startBeginIndex == -1) { return undefined; }

        const startEndIndex = startBeginIndex + start.length;
        const endBeginIndex = searchString.indexOf(end, startEndIndex);
        if (endBeginIndex == -1) { return undefined; }

        const retVal: IFindIndexResult = {
            start: { begin: startBeginIndex, end: startEndIndex },
            end: { begin: endBeginIndex, end: endBeginIndex + end.length },
            between: searchString.substring(startEndIndex, endBeginIndex),
            fullLength: endBeginIndex + end.length - startBeginIndex,
        };
        return retVal;
    }

    public static substringBetween(sourceString: string, start: string, end?: string) {
        let startIndex = sourceString.indexOf(start);
        if (startIndex < 0) {
            return null;
        }
        startIndex = startIndex + start.length;

        let endIndex = sourceString.length;
        if (end) {
            endIndex = sourceString.indexOf(end, startIndex);
            if (endIndex < 0) {
                return null;
            }
        }
        return sourceString.substring(startIndex, endIndex);
    }

    public static replaceString(searchString: string, startIndex: number, endIndex: number, replaceString: string) {
        const newReplaceString = replaceString.split("\\").join("/");
        const newString = searchString.substring(0, startIndex) + newReplaceString + searchString.substring(endIndex);
        return newString;
    }
    public static replaceStringWRes(searchString: string, findRes: IFindIndexResult, replaceString: string) {
        const newString = this.replaceString(searchString, findRes.start.begin, findRes.end.end, replaceString);
        return newString;
    }

    public static trimEnd(text: string, trimString: string) {
        const len = trimString.length;
        if (len <= 0) { return text; }

        let retVal = text;
        while(retVal.endsWith(trimString)) { retVal = retVal.substring(0, retVal.length - len); }
    }
    public static trimStart(text: string, trimString: string) {
        const len = trimString.length;
        if (len <= 0) { return text; }

        let retVal = text;
        while (retVal.startsWith(trimString)) { retVal = retVal.substring(len); }
        return retVal;
    }
    public static trim(text: string, trimString: string) {
        return this.trimEnd(this.trimStart(text, trimString), trimString);
    }
}